$orange: #F25B24;
$black: #2E2D35;
$gradient: linear-gradient(90deg, #2D1686 0%, #4E33B5 100%);
body {
    font-family: 'Whitney', sans-serif;
    padding: 0;
    margin: 0;
    color: #fff;
}

.content {
    max-width: 1220px;
    padding: 0 25px;
    margin: 0 auto;
    box-sizing: border-box;
}

.btn {
    background: #F25B24;
    border-radius: 4px;
    padding: 16px 60px;
    border: none;
    outline: none;
    font-weight: 600;
    font-size: 18px;
    line-height: 100%;
    cursor: pointer;
    color: #fff;
}

.header {
    background: $gradient;
    padding: 33px 0;
    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__item {
        max-height: 32px;
    }
    &__logo {
        img {
            height: 100%;
        }
    }
    &__right {
        display: flex;
        flex: 0 0 auto;
        font-weight: 325;
        font-size: 18px;
        line-height: 23px;
    }
    &__client {
        margin-right: 100px;
    }
    &__auth {
        cursor: pointer;
        display: flex;
        align-items: center;
        img {
            margin-right: 12px;
            width: 24px;
            height: 24px;
        }
    }
}

.firstscreen {
    background: $gradient;
    &__wrapper {
        background-image: url("../img/auto.png");
        background-repeat: no-repeat;
        background-position: center right;
        padding: 100px 0 180px;
        background-size: auto;
    }
    &__left {
        max-width: 515px;
    }
    &__title {
        font-weight: 600;
        font-size: 50px;
        line-height: 120%;
        margin: 0 0 30px;
    }
    &__subtitle {
        font-weight: 350;
        font-size: 24px;
        line-height: 31px;
        margin-bottom: 60px;
    }
}

.opportunity {
    padding: 130px 0;
    &__title {
        margin: 0 0 50px;
        font-weight: 600;
        font-size: 40px;
        line-height: 52px;
        color: $black;
    }
    &__list {
        display: flex;
        flex-wrap: wrap;
        row-gap: 30px;
        column-gap: 30px;
        box-sizing: border-box;
    }
    &__item {
        flex: 0 0 50%;
        max-width: calc(50% - 15px);
        background: #F6F5F9;
        border-radius: 6px;
        padding: 21px 10px;
        display: flex;
        justify-content: space-between;
        box-sizing: border-box;
    }
    &__img {
        flex: 0 0 193px;
        img {
            max-width: 100%;
        }
    }
    &__left {
        flex-grow: 1;
        padding: 22px 35px 22px 30px;
    }
    &__name {
        font-weight: 600;
        font-size: 22px;
        line-height: 29px;
        color: $black;
        margin: 0 0 22px;
    }
    &__descr {
        font-weight: normal;
        font-size: 16px;
        line-height: 142%;
        color: #474351;
    }
}

.request {
    padding-bottom: 185px;
    &__wrapper {
        padding: 70px;
        box-sizing: border-box;
        border-radius: 10px;
        background: url("../img/cars.webp") center right no-repeat, $gradient;
        background-size: auto 90%;
    }
    &__text {
        font-weight: 600;
        font-size: 34px;
        line-height: 45px;
        margin-bottom: 46px;
        max-width: 560px;
    }
}

.modal {
    display: none;
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(28, 24, 47, 0.4);
    width: 100%;
    min-height: 100vh;
    height: auto;
    &__close {
        width: 15px;
        height: 15px;
        position: absolute;
        top: 24px;
        right: 24px;
        cursor: pointer;
        img {
            width: 100%;
            height: 100%;
        }
    }
    &__wrapper {
        box-sizing: border-box;
        width: 490px;
        background: #FFFFFF;
        box-shadow: 0 10px 60px rgba(0, 0, 0, 0.25);
        border-radius: 6px;
        padding: 60px 60px 55px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    &__title {
        font-weight: 600;
        font-size: 40px;
        line-height: 52px;
        text-align: center;
        color: $black;
        margin: 0 0 35px;
    }
    label {
        font-weight: 350;
        font-size: 18px;
        line-height: 24px;
        color: $black;
        margin-bottom: 10px;
        display: block;
    }
    input {
        width: 100%;
        margin-bottom: 18px;
        border: 1px solid #DDDAE7;
        box-sizing: border-box;
        border-radius: 4px;
        font-weight: 325;
        font-size: 16px;
        padding: 17px 12px;
        line-height: 100%;
        color: #474351;
        &:focus {
            outline: none;
        }
    }
    &__btn {
        margin-top: 17px;
        width: 100%;
    }
}

@media (max-width: 1440px) {
    .firstscreen__wrapper {
        background-size: 50% auto;
    }
}

@media (max-width: 1199px) {
    .firstscreen__title {
        font-size: 40px;
        margin-bottom: 20px;
    }
    .firstscreen__left {
        max-width: 50%;
    }
    .firstscreen__subtitle {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 40px;
    }
    .firstscreen__wrapper {
        padding: 80px 0 100px;
    }
    .opportunity__title {
        margin: 0 0 30px;
        font-size: 30px;
        line-height: 38px;
    }
}

@media (max-width: 991px) {
    .header__item {
        height: 24px;
    }
    .opportunity__left {
        padding: 0 15px 0 10px;
    }
    .opportunity__img {
        flex: 0 0 36%;
        max-width: 36%;
    }
    .opportunity {
        padding: 70px 0;
    }
    .opportunity__name {
        font-size: 18px;
        line-height: 24px;
        margin: 0 0 15px;
    }
    .opportunity__descr {
        font-size: 14px;
        color: #474351;
    }
    .btn {
        padding: 12px 40px;
        font-size: 15px;
        line-height: 100%;
    }
    .header {
        padding: 20px 0;
    }
    .request__wrapper {
        padding: 40px;
    }
    .request__text {
        margin-bottom: 30px;
    }
}

@media (max-width: 767px) {
    .header__client {
        margin-right: 20px;
    }
    .firstscreen__wrapper {
        padding: 50px 0;
        background-image: none;
    }
    .firstscreen__left {
        max-width: unset;
    }
    .modal__wrapper {
        max-width: 420px;
        width: 100%;
        padding: 30px;
        position: absolute;
    }
    .modal__title {
        font-size: 30px;
        line-height: 35px;
        margin: 0 0 25px;
    }
    .modal input {
        padding: 14px 8px;
    }
    .opportunity__item {
        max-width: unset;
        margin-bottom: 20px;
    }
    .opportunity__list {
        display: block;
    }
    .opportunity {
        padding: 40px 0;
    }
    .opportunity__title {
        font-size: 25px;
        line-height: 120%;
    }
    .request__text {
        font-size: 24px;
        line-height: 31px;
    }
    .request__wrapper {
        padding: 20px;
        background-size: contain;
    }
}

@media (max-width: 425px) {
    .firstscreen__title {
        font-size: 28px;
    }
    .content {
        padding: 0 20px;
    }
    .header__auth img {
        width: 20px;
        height: 20px;
    }
    .header__right {
        font-size: 16px;
        line-height: 18px;
    }
    .header__item {
        height: 20px;
    }
    .firstscreen__subtitle {
        font-size: 15px;
        line-height: 20px;
        margin-bottom: 25px;
    }
    .opportunity__title {
        font-size: 22px;
    }
    .opportunity__left {
        padding: 0;
    }
    .opportunity__img {
        max-width: 22%;
    }
    .request__text {
        font-size: 22px;
        line-height: 28px;
    }
}